.header {
  background: rgba(16, 16, 29, 0.90);
  position: fixed;
  top: 0;
  width: 100%;
}

.header__container {
  max-width: 1165px;
  margin: 0 auto;
  padding: 33.5px 0 31.5px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 1165px) {
    padding: 33.5px 20px 31.5px;
  }
}

.header__nav {
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 60px;

  @media (max-width: 768px) {
    display: none;
    position: absolute;
    top: 55px;
    left: 0;
    width: 100%;
    background: rgba(16, 16, 29, 1);
    z-index: 2;
    height: auto;
    padding: 10px 20px;
    margin-top: 33px;
  }

  &--active {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
  }
}

.header__link {
  color: #FFF;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  cursor: pointer;

  &:hover {
    color: #43ABF0;
  }

  &--active {
    color: #43ABF0;
  }

  @media (max-width: 768px) {
    font-size: 14px;
  }
}

.header__catalog {
  display: none;
}

@media (max-width: 768px) {
  .header__burger {
    margin: 3px 0 0 -5px;
    cursor: pointer;
    z-index: 3;
    width: 22px;
    transform: translateZ(0);
    backface-visibility: hidden;
  }

  .header__line {
    display: block;
    width: 20px;
    height: 2px;
    background-color: #FFFFFF;
    margin: 5px 0 0;
    position: relative;
    transition: .4s ease-out;

    &:first-child {
      margin-top: 0;
    }
  }

  .header__catalog {
    display: flex;
    position: relative;
  }

  .header__checkbox {
    display: none;
  }

  .header__checkbox:checked ~ .header__burger .header__line_number_one {
    width: 25px;
    top: 3px;
    transform: rotate(225deg);
    transition: .4s ease-out;
  }

  .header__checkbox:checked ~ .header__burger .header__line_number_two {
    opacity: 0;
  }

  .header__checkbox:checked ~ .header__burger .header__line_number_three {
    width: 25px;
    top: -4px;
    transform: rotate(-225deg);
    transition: .4s ease-out;
  }
}
