.form {
  background: url("../../../img/background2.png"), rgba(16, 16, 29, 0.90) 50% / cover no-repeat;
  padding: 150px 0;
}

.form__container {
  max-width: 1165px;
  margin: 0 auto;
  text-align: center;
  color: #FFFFFF;

  @media (max-width: 1165px) {
    padding: 0 20px;
  }
}

.form__title {
  color: #FFF;
  font-size: 36px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 30px;

  &--color {
    color: #3E9CDC;
  }
}

.form__subtitle {
  color: #BBB;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.form__list {
  padding: 0;
  list-style: none;
  margin-top: 80px;
  display: flex;
  align-items: flex-start;
  gap: 30px;
  background: url("../../../img/points.svg") 50% 35% no-repeat;

  @media (max-width: 800px) {
    background: transparent;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.form__item {
  display: flex;
  width: 209px;
  padding: 0 10px;
  flex-direction: column;
  align-items: center;
  gap: 20px;

  @media (max-width: 800px) {
    width: 150px;
  }
}

.form__circle {
  width: 100px;
  height: 100px;
  background: url("../../../img/circle.svg") center no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 800px) {
    width: 70px;
    height: 70px;
    background: url("../../../img/circle.svg") 100% 100%/ 100% 100% no-repeat;
  }
}

.form__img {
  @media (max-width: 800px) {
    width: 40px;
  }
}

.form__text {
  color: #FFF;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  @media (max-width: 800px) {
    font-size: 12px;
  }
}

.form__wrapperFile {
  position: relative;

  @media (max-width: 400px) {
    width: 92%;
  }
}

.form__file {
  opacity: 0;
  cursor: pointer;
}

.form__fileLabel {
  position: absolute;
  top: 0;
  display: flex;
  width: 100%;
  padding: 10px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
  border-radius: 3px;
  border: 2px solid #3E9CDC;
  background: #FFF;
  color: #272733;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  cursor: pointer;
}

.form__wrapper {
  margin: 80px 0 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;

  @media (max-width: 800px) {
    grid-template-columns: 1fr;
  }
}

.form__wrapperSecond {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 56px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.form__rangeContainer {
  width: 100%;

  @media (max-width: 400px) {
    width: 92%;
  }
}

.form__rangeWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.form__range {
  -webkit-appearance: none;
  width: 100%;
  height: 6px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.70);
  outline: none;
  -webkit-transition: .2s;
  transition: opacity .2s;
  border: 1px solid #FFFFFF;

  &:hover {
    opacity: 1;
  }

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    background: #42A9ED;
    border: 4px solid #286690;
    cursor: pointer;
  }

  &::-moz-range-thumb {
    width: 37px;
    height: 37px;
    border-radius: 50%;
    background: #42A9ED;
    border: 4px solid #286690;
    cursor: pointer;
  }
}

.form__rangeText {
  color: #FFF;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;

  @media (max-width: 400px) {
    font-size: 12px;
  }
}

.form__input {
  color: #272733;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding: 8px 10px;
  border-radius: 3px;
  border: 1px solid #FFF;
  background: rgba(255, 255, 255, 0.85);
}

.form__button {
  margin-top: 55px;
  width: 370px;
  padding: 15px 0;
  border-radius: 4px;
  border: 1px solid #FFF;
  background: #3E9CDC;
  color: #FFF;
  text-align: center;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  cursor: pointer;

  &:hover {
    opacity: .8;
  }

  @media (max-width: 400px) {
    width: 100%;
  }
}