.start {
  background: url("../../../img/background.png") top no-repeat;
  background-size: cover;
  padding: 150px 0;
}

.start__container {
  display: flex;
  flex-direction: column;
  gap: 80px;
  align-items: flex-start;
  max-width: 1165px;
  margin: 30px auto 0;

  @media (max-width: 1165px) {
    padding: 0 20px;
  }
}

.start__title {
  color: #000;
  font-size: 48px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  max-width: 800px;

  @media (max-width: 768px) {
    font-size: 30px;
  }
}

.start__titleColor {
  color: #3E9CDC;
}

.start__text {
  color: #000;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 10px;

  @media (max-width: 768px) {
    font-size: 16px;
  }
}

.start__list {
  padding: 0 0 0 25px;
  list-style: none;
}

.start__item {
  color: #000;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 5px;
  position: relative;

  &:before {
    position: absolute;
    content: "";
    width: 4.94px;
    height: 1.51px;
    background: #000;
    top: 9px;
    left: -15px;
  }

  @media (max-width: 768px) {
    font-size: 16px;
  }
}

.start__wrapperButton {
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
}

.start__button {
  color: #FFF;
  text-align: center;
  font-size: 13px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 15px 0;
  width: 183px;
  border-radius: 4px;
  border: 1px solid #FFF;
  background: #3E9CDC;
  display: inline-block;
  text-decoration: none;
  line-height: normal;

  &:hover {
    opacity: .8;
  }

  &--white {
    border: 1px solid #6F6D7B;
    background: #FFF;
    color: #2A2B30;
  }
}