.footer {
  background: #3D4050;
  padding: 30px 0;
}

.footer__container {
  max-width: 1165px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 1165px) {
    padding: 0 20px;
  }

  @media (max-width: 680px) {
    flex-direction: column;
    gap: 20px;
  }
}

.footer__line {
  @media (max-width: 680px) {
    display: none;
  }
}

.footer_list {
  display: flex;
  align-items: center;
  gap: 38px;
  padding: 0;
  list-style: none;

  @media (max-width: 1165px) {
    flex-direction: column;
  }

  @media (max-width: 680px) {
    gap: 20px;
  }
}

.footer__link {
  display: flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;

  &:hover {
    opacity: .8;
  }
}

.footer__img {
  width: 20px;
}

.footer__title {
  color: #FFF;
  font-size: 13px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;

  &--text {
    text-transform: uppercase;
  }
}

.footer__text {
  color: #C3C3C3;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  &--size {
    text-transform: uppercase;
  }
}