.select {
  position: relative;
  width: 100%;
  height: 40px;
  margin: 0 auto;
  color: #272733;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  &[data-state="active"] {
    .select__title {
      border-radius: 3px 3px 0 0;
      background: rgba(61, 64, 80, 0.85);
      color: #FFFFFF;
      border: none;
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
        background: #FFFFFF;
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
        background: #FFFFFF;
      }
    }

    .select__content {
      opacity: 1;
    }

    .select__label + .select__input + .select__label {
      max-height: 40px;
      border-top-width: 1px;
    }
  }
}
.select__title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.85);
  border: solid 1px #c7ccd1;
  cursor: pointer;
  border-radius: 3px 3px 0 0;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 50%;
    right: 16px;

    display: block;
    width: 10px;
    height: 2px;

    transition: all 0.3s ease-out;

    background-color: #333333;

    transform: translate(-3px, -50%) rotate(45deg);
  }
  &::after {
    transform: translate(3px, -50%) rotate(-45deg);
  }

}
.reset {
  display: flex;
  width: 230px;
  padding: 8px 16px;
  margin: 0 auto 10px;

  border: solid 1px #c7ccd1;
  border-radius: 5px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  font-weight: bold;

  background: rgba(39, 39, 51, 0.90);
  color: #333333;

  &:hover {
    background: rgba(39, 39, 51, 0.90);
    color: #ffffff;
  }
}
.select__content {
  position: absolute;
  top: 40px;
  left: 3px;

  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);

  background-color: #ffffff;

  border: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

  transition: all 0.3s ease-out;

  opacity: 0;
  z-index: 8;
}
.select__input {
  display: none;

  &:checked + label {
    background-color: #dedede;
  }
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}
.select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 16px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  overflow: hidden;
  background: rgba(39, 39, 51, 1);
  color: #ffffff;
  border: none;

  & + input + & {
    border-top: none;
  }

  &:hover {
    color: #42A9ED;
  }
}